      .bloque-texto-g__texto.p-4
        p.mb-0(data-aos="fade-down") La ruta metodolgica del enfoque Un Pueblo, Un Producto (OVOP) orienta el proceso de implementacin de la estrategia de desarrollo local incluyente, permitiendo que las comunidades avancen de manera organizada, participativa y sostenible hacia la construccin de su sueo colectivo. Esta metodologa fue estructurada por el SENA (2020) en doce guas prcticas, que explican paso a paso las fases, actividades y herramientas necesarias para que las comunidades reconozcan sus potencialidades, fortalezcan sus capacidades locales y promuevan el desarrollo territorial.
    p.mb-5(data-aos="fade-down") Cada fase tiene un propsito especfico, pero todas estn interrelacionadas, conformando un proceso continuo de aprendizaje, accin y consolidacin. La ruta metodolgica se desarrolla en cinco fases principales, que se describen a continuacin:
    .tarjeta.tarjeta--BG04.p-4.mb-5
      LineaTiempoC.color-acento-botones(text-small)(data-aos="flip-down")
        .row(titulo="Fase 1")
          .col-lg-6.mb-4.mb-md-0.order-lg-2.order-2
            h4 Preparacin
            p En esta etapa se establecen las condiciones iniciales para la implementacin de la estrategia. Se identifican los actores clave, se define el territorio de intervencin y se realiza un diagnstico participativo de las potencialidades locales. Adems, se genera un espacio de sensibilizacin comunitaria para introducir el enfoque OVOP y fortalecer la motivacin colectiva.
          .col-lg-6.order-lg-1.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema4/img02.jpg', alt='Imagen decorativa')
        .row(titulo="Fase 2")
          .col-lg-6.mb-4.mb-md-0.order-lg-2.order-2
            h4 Introduccin
            p Su objetivo es promover el conocimiento del enfoque OVOP en la comunidad y crear las bases para la construccin del sueo colectivo. Se fomenta la participacin de los diferentes grupos poblacionales y se fortalecen las capacidades locales para garantizar que el proceso sea inclusivo y representativo.
          .col-lg-6.order-lg-1.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema4/img03.jpg', alt='Imagen decorativa')
        .row(titulo="Fase 3")
          .col-lg-6.mb-4.mb-md-0.order-lg-2.order-2
            h4 Planificacin y desarrollo
            p En esta fase se definen las estrategias y acciones que orientarn el desarrollo del producto o servicio representativo del territorio. Incluye la identificacin de los tesoros locales, la planificacin de actividades productivas, la definicin de roles comunitarios y la elaboracin de un plan de trabajo con metas claras y alcanzables.
          .col-lg-6.order-lg-1.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema4/img04.jpg', alt='Imagen decorativa')
        .row(titulo="Fase 4")
          .col-lg-6.mb-4.mb-md-0.order-lg-2.order-2
            h4 Establecimiento
            p Consiste en la implementacin de las acciones definidas en la fase anterior. Aqu se consolidan las alianzas entre actores institucionales, productivos y comunitarios, y se promueve la autogestin de los procesos. Es una etapa clave para fortalecer las capacidades tcnicas, organizativas y comerciales del grupo comunitario.
          .col-lg-6.order-lg-1.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema4/img05.jpg', alt='Imagen decorativa')
        .row(titulo="Fase 5")
          .col-lg-6.mb-4.mb-md-0.order-lg-2.order-2
            h4 Sostenimiento
            p Busca garantizar la continuidad de los resultados alcanzados. Se enfoca en la generacin de capacidades locales permanentes, la creacin de redes colaborativas, el posicionamiento del producto en mercados ms amplios y la consolidacin de un liderazgo compartido que mantenga vivo el sueo colectivo.
          .col-lg-6.order-lg-1.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema4/img06.jpg', alt='Imagen decorativa')
    p.mb-5(data-aos="fade-down") La metodologa se organiza en temas estratgicos, y cada gua prctica (DM) se relaciona con un componente del proceso:
