        p.mb-5(data-aos="fade-down") Desde el enfoque OVOP, es fundamental analizar la potencialidad de estos recursos, entendiendo que deben ser potenciadores del desarrollo local, no solo en trminos de competitividad, sino tambin de inclusin social. Para ello, se recomienda que los tesoros identificados sean evaluados con base en las siguientes variables:
