    separador
    #t_4_2.titulo-segundo.color-acento-contenido(data-aos="flip-down")
      h2 4.2 Gua para la construccin del sueo colectivo OVOP
    .row.justify-content-center.mb-5
      .col-lg-8.order-lg-1.order-2
        p(data-aos="fade-down") Dentro de la ruta metodolgica de OVOP Colombia, la construccin del sueo colectivo representa un paso esencial en la Fase 2, correspondiente a la definicin del producto o productos del territorio. Su propsito es generar valor en torno a Mi Producto, fortaleciendo la identidad, la visin compartida y el compromiso comunitario con el desarrollo local.
        .cajon.cajon.C01.color-primario.p-4.mb-3
          p.mb-0(data-aos="fade-down") El sueo colectivo se concibe como una visin deseada del territorio en el futuro, construida de manera participativa. A travs de este proceso, las comunidades expresan sus aspiraciones y definen de forma conjunta el rumbo que desean para su desarrollo. Esta visin se convierte en un motor de transformacin, que inspira la accin y orienta la planificacin estratgica de las iniciativas locales.
        p(data-aos="fade-down") En el enfoque OVOP Colombia, el sueo colectivo se construye mediante la metodologa de <em>backcasting</em>, la cual parte del futuro deseado para definir los pasos necesarios desde el presente. A diferencia del forecasting, que proyecta el futuro desde las condiciones actuales, el <em>backcasting</em> propone imaginar un escenario ideal y trabajar hacia su materializacin. Este enfoque estimula la creatividad, la innovacin y el sentido de posibilidad dentro de las comunidades.
      .col-lg-4.order-lg-2.order-1.mb-3.col-md-5.col-7
        figure(data-aos="flip-down") 
          img(src='@/assets/curso/temas/tema4/img23.png', alt='Imagen decorativa')
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 2.
          span <em>Perspectiva de <em>backcasting</em></em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img24.svg', alt='La figura 2 presenta el modelo de planificacin de backcasting en el cual la comunidad define una meta futura deseable, el sueo colectivo, y a partir de ella planifica las acciones necesarias desde el presente para alcanzarla. Se contrasta con la perspectiva de forecasting, que pronostica el futuro basndose en las condiciones y recursos actuales. El enfoque OVOP adopta el backcasting para orientar el desarrollo local desde una visin compartida de futuro.')
            figcaption Nota. Tomado de la Gua 2 OVOP Colombia (SENA, JICA, MinCIT, 2020).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img25.svg', alt='La figura 2 presenta el modelo de planificacin de backcasting en el cual la comunidad define una meta futura deseable, el sueo colectivo, y a partir de ella planifica las acciones necesarias desde el presente para alcanzarla. Se contrasta con la perspectiva de forecasting, que pronostica el futuro basndose en las condiciones y recursos actuales. El enfoque OVOP adopta el backcasting para orientar el desarrollo local desde una visin compartida de futuro.')
          figcaption Nota. Tomado de la Gua 2 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-5(data-aos="flip-down")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema4/img26.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Como parte de las experiencias que expone la aplicacin del enfoque OVOP en Colombia, se destaca el caso de Mompox: Vivir es arte, una iniciativa que refleja cmo las comunidades pueden construir una visin compartida de su territorio a partir de sus recursos culturales, artsticos y sociales.
        br
        br
        p.mb-0 En esta experiencia, los actores locales unieron esfuerzos para proyectar un futuro basado en la identidad patrimonial y el arte como motor de desarrollo. La actividad permiti materializar el sueo colectivo mediante un proceso participativo de planeacin, fortaleciendo la cohesin social y el sentido de pertenencia al territorio.
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 3.
          span <em>Aproximacin visin territorial</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img27.svg', alt='La figura 3 representa la experiencia territorial Mompox: vivir es arte, donde la comunidad construye su visin de futuro a partir de su identidad cultural, creatividad y sentido de pertenencia. Incluye elementos como el territorio, la cultura, los valores y las aspiraciones colectivas, que evidencian la aplicacin de la metodologa participativa del enfoque OVOP.')
            figcaption Nota. Tomado de la Gua 2 OVOP Colombia (SENA, JICA, MinCIT, 2020 ).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img28.svg', alt='La figura 3 representa la experiencia territorial Mompox: vivir es arte, donde la comunidad construye su visin de futuro a partir de su identidad cultural, creatividad y sentido de pertenencia. Incluye elementos como el territorio, la cultura, los valores y las aspiraciones colectivas, que evidencian la aplicacin de la metodologa participativa del enfoque OVOP.')
          figcaption Nota. Tomado de la Gua 2 OVOP Colombia (SENA, JICA, MinCIT, 2020 ).
    p.mb-5(data-aos="fade-down") Para llevar a cabo este proceso de manera estructurada, es fundamental contar con una gua que oriente cada etapa de trabajo comunitario. A continuacin, se presenta la ficha tcnica que describe las actividades, recursos y condiciones necesarias para facilitar la construccin participativa del sueo colectivo en los territorios.
