    .row.justify-content-center.mb-4
      .col-lg-10.order-lg-2.order-2
        p(data-aos="fade-down") En el enfoque Un Pueblo, Un Producto (OVOP), la ideacin del producto constituye una fase esencial dentro de la ruta metodolgica, pues representa el momento en el que la comunidad transforma sus recursos, conocimientos y creatividad en propuestas concretas de valor para el territorio. Este proceso busca impulsar la generacin de ideas innovadoras que fortalezcan la identidad local y promuevan el desarrollo sostenible.
        p.mb-0(data-aos="fade-down") En esta etapa, las comunidades reflexionan sobre sus recursos, potencialidades y aspiraciones colectivas para definir qu producto o servicio puede representar mejor su identidad y contribuir a su desarrollo local. Comprender los conceptos de idea, creacin de valor e innovacin es fundamental, ya que estos orientan la manera en que se construyen propuestas sostenibles, inclusivas y coherentes con la filosofa OVOP.
      .col-lg-2.order-lg-1.order-1.mb-3.col-md-5.col-7
        figure(data-aos="flip-down") 
          img(src='@/assets/curso/temas/tema4/img29.svg', alt='Imagen decorativa')
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-8.col-12.mb-md-0
        AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--C04")
          .row(titulo="Idea")
            .col-12.mb-0.mb-md-0
              p Se concibe como la expresin de un pensamiento o propuesta que introduce una forma nueva de crear, ofrecer o experimentar algo. En el marco del enfoque OVOP, cualquier persona puede generar ideas, sin importar su nivel tcnico o profesional, ya que se reconoce y valora la diversidad de saberes y perspectivas presentes en la comunidad. No obstante, la generacin de ideas requiere preparacin, anlisis y un conocimiento profundo del territorio y de los recursos disponibles. En este sentido, la ideacin se configura como un proceso participativo que impulsa la creatividad colectiva y fomenta la construccin de soluciones innovadoras orientadas al desarrollo local.
          .row(titulo="Creacin de valor")
            .col-12.mb-0.mb-md-0
              p Dentro del enfoque OVOP, la creacin de valor implica que las comunidades y las instituciones reflexionen sobre cmo aprovechar y potenciar los recursos disponibles en su territorio. Este proceso busca transformar recursos tangibles e intangibles, como los culturales, histricos, arquitectnicos, productivos o ambientales, en productos o servicios que generen beneficios econmicos, sociales y culturales.
              p La comunidad debe reconocer dos caminos posibles: crear valor, que significa generar algo completamente nuevo (pasar de 0 a 1), o agregar valor, que consiste en mejorar o transformar algo ya existente (pasar de 1 en adelante). Ambas vas contribuyen a la construccin de productos locales con proyeccin global, en coherencia con el principio OVOP de pensar global y actuar local.
          .row(titulo="Innovacin")
            .col-12.mb-0.mb-md-0
              p En el marco del enfoque OVOP, se entiende como el proceso mediante el cual se introducen cambios o mejoras significativas que generan beneficios sostenibles para la comunidad. Puede manifestarse en productos, servicios o procesos, y suele surgir de la combinacin entre elementos tradicionales y nuevas ideas, tecnologas o mtodos. La innovacin fortalece la competitividad local, promueve la sostenibilidad y contribuye a mejorar la calidad de vida de las comunidades, al tiempo que potencia su capacidad de adaptacin frente a los cambios del entorno.
      .col-lg-4.col-6.col-md-6.col-8.order-lg-2.order-1.mb-lg-0.mb-3.d-none.d-lg-block
        figure
          img(src='@/assets/curso/temas/tema4/img30.png', alt='Imagen decorativa')
    p.mb-5(data-aos="fade-down") Con el propsito de orientar el proceso prctico de esta fase, se presenta la ficha tcnica correspondiente a la ideacin de productos. Esta herramienta metodolgica ofrece una gua estructurada para el desarrollo del taller, detallando los objetivos, condiciones previas, actividades, tiempos, materiales y metodologas recomendadas. Su aplicacin facilita la organizacin del trabajo comunitario, la generacin de ideas colectivas y la construccin de propuestas innovadoras alineadas con el enfoque OVOP.
