    separador
    #t_4_4.titulo-segundo.color-acento-contenido(data-aos="flip-down")
      h2 4.4 Gua para el estudio de Cadena de Valor Simple y seleccin de Mi Producto OVOP
    p.mb-5(data-aos="fade-down") El anlisis de la cadena de valor constituye una herramienta fundamental para comprender cmo se generan los procesos productivos, econmicos y sociales en el territorio, y cmo cada actor aporta a la creacin de valor. En el marco del enfoque OVOP, esta etapa permite identificar oportunidades para fortalecer la competitividad local, mejorar la distribucin de beneficios y orientar las estrategias hacia un desarrollo equitativo y sostenible.
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 a) Cadena de valor y su anlisis
    .row.justify-content-center.mb-0
      .col-12
        .row.justify-content-center
          .col-lg-10.col-12.order-lg-2.order-2
            .tarjeta.tarjeta--C01.p-4.mb-5
              .row.justify-content-center.align-items-center
                .col-lg-4.col-8.col-md-7.order-lg-1.order-1.mb-3
                  figure
                    img(src='@/assets/curso/temas/tema4/img31.svg', alt='Imagen decorativa')
                .col-lg-8.col-12.order-lg-2.order-2
                  p(data-aos="fade-down") Es un modelo terico propuesto por Michael Porter que permite describir las actividades de una organizacin o empresa para generar valor tanto para el cliente como para s misma. Desde el enfoque OVOP, analizar la cadena de valor de los productos locales facilita determinar la creacin de valor agregado, su relacin con la generacin de ingresos y la distribucin de beneficios entre los actores que participan en la produccin. Esta informacin es clave para orientar la planificacin comunitaria y fortalecer el desarrollo econmico del territorio.
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 b) Economa sana segn el enfoque OVOP
    p.mb-5(data-aos="fade-down") Se refiere a un modelo local en el que la comunidad se empodera para crear valor, innovar y mejorar continuamente sus procesos. Este enfoque busca superar las dificultades estructurales de la economa local, incrementando la competitividad territorial y la calidad de vida de sus habitantes. Una economa sana se caracteriza por la equidad en la distribucin de beneficios, el fortalecimiento del consumo interno y la generacin de un crecimiento sostenible que beneficie a toda la comunidad.
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 c) Productos OVOP y productos estrella
    .row.justify-content-center.mb-5
      .col-lg-7.order-lg-2.order-2
        p(data-aos="fade-down") Los productos OVOP son aquellos que renen caractersticas fundamentales para promover el desarrollo local y empoderar a las comunidades. Para que un producto sea considerado como OVOP, debe surgir de un proceso participativo que identifique los recursos locales y los transforme en tesoros, es decir, bienes con potencial de aprovechamiento comunitario.
        .cajon.cajon.C01.color-primario.p-4.mb-3
          p.mb-0(data-aos="fade-down") Los productos potenciales son aquellos recursos o bienes que tienen valor agregado o pueden generarlo, incrementando los ingresos de la comunidad y la competitividad del territorio. Sin embargo, solo se consideran productos OVOP aquellos cuya produccin o comercializacin involucra activamente a la comunidad y representa la identidad del territorio.
        p(data-aos="fade-down") Por su parte, los productos estrella son aquellos con alta participacin en el mercado y potencial de crecimiento sostenido. En el enfoque OVOP, estos productos se denominan Mi Producto, ya que simbolizan la consolidacin del desarrollo local con impacto global.
      .col-lg-5.order-lg-1.order-1.mb-3.col-md-5.col-7
        figure(data-aos="flip-down") 
          img(src='@/assets/curso/temas/tema4/img32.png', alt='Imagen decorativa')
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 4.
          span <em>Transicin de recursos locales a productos OVOP</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img33.svg', alt='La figura 4 representa el proceso de transformacin de los recursos locales en productos OVOP. Inicia con la conciencia comunitaria, contina con la identificacin de recursos locales, su valoracin como recursos potenciales, el agregado de valor para generar productos potenciales, la comercializacin comunitaria y finalmente la consolidacin de productos OVOP.')
            figcaption Nota. Tomado de la Gua 4 OVOP Colombia (SENA, JICA, MinCIT, 2020 ).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img34.svg', alt='La figura 4 representa el proceso de transformacin de los recursos locales en productos OVOP. Inicia con la conciencia comunitaria, contina con la identificacin de recursos locales, su valoracin como recursos potenciales, el agregado de valor para generar productos potenciales, la comercializacin comunitaria y finalmente la consolidacin de productos OVOP.')
          figcaption Nota. Tomado de la Gua 4 OVOP Colombia (SENA, JICA, MinCIT, 2020 ).
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 d) Temas preliminares
    p.mb-5(data-aos="fade-down") Dentro de la ruta metodolgica de OVOP se presentan diversas herramientas que facilitan la obtencin de resultados preliminares. Despus del anlisis de la cadena de valor, existen pasos adicionales que fortalecen el proceso de desarrollo territorial. Estos incluyen el estudio de la estructura productiva local, la evaluacin de recursos estratgicos y la revisin de informacin econmica.
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 5.
          span <em>Asuntos preliminares y posteriores al anlisis de la cadena de valor</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img35.svg', alt='En la figura 5 se presentan las fases del proceso OVOP: Fase I, evaluacin de Mi Pueblo, identificacin de recursos locales e ideacin de productos; Fase II, anlisis de productos potenciales, formacin de redes de negocio colectivo y definicin del producto OVOP; y Fase III, gestin del producto, presentacin a la comunidad y elaboracin del plan de negocio basado en la estrategia de marca territorial. Cada fase incluye actividades y resultados esperados como fichas tcnicas, listados de tesoros locales y modelos de negocio.')
            figcaption Nota. Tomado de la Gua 4 OVOP Colombia (SENA, JICA, MinCIT, 2020).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img36.svg', alt='En la figura 5 se presentan las fases del proceso OVOP: Fase I, evaluacin de Mi Pueblo, identificacin de recursos locales e ideacin de productos; Fase II, anlisis de productos potenciales, formacin de redes de negocio colectivo y definicin del producto OVOP; y Fase III, gestin del producto, presentacin a la comunidad y elaboracin del plan de negocio basado en la estrategia de marca territorial. Cada fase incluye actividades y resultados esperados como fichas tcnicas, listados de tesoros locales y modelos de negocio.')
          figcaption Nota. Tomado de la Gua 4 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 e) Ficha tcnica municipal
    p.mb-4(data-aos="fade-down") Para estimar el impacto de Mi Producto en la economa local, es necesario recopilar informacin geogrfica, econmica y social del territorio donde se desarrolla la iniciativa.
    .row.justify-content-center.mb-5
      .col-lg-10.col-12
        .tarjeta.tarjeta--C02.p-4(data-aos="fade-right")
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-auto
              img(src="@/assets/curso/temas/tema4/img37.svg")
            .col
              .row.justify-content-center.align-items-center
                .col.mb-sm-0.mb-4
                  p La ficha tcnica municipal consolida datos relevantes sobre la composicin poblacional, los hogares, la extensin territorial y las principales actividades econmicas, lo que permite contextualizar los resultados del anlisis de la cadena de valor. Para ampliar la informacin, consultar el documento anexo: <b>Ficha_de_analisis_cadena_de_valor_para_instructores_OVOP.</b>
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('/downloads/Ficha_de_analisis_cadena_de_valor_para_instructores_OVOP.xlsx')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 f) Resultados de descubrimiento de tesoros locales
    p.mb-4(data-aos="fade-down") En las primeras fases del enfoque OVOP se lleva a cabo el redescubrimiento de los recursos territoriales, valorando aquellos que la comunidad reconoce como tesoros locales. Estos deben analizarse segn su identidad, unicidad y potencial de aprovechamiento, con el fin de determinar cules podran transformarse en productos OVOP. Los resultados de este ejercicio deben documentarse, pues constituyen la base para el anlisis de la cadena de valor y reflejan la perspectiva comunitaria frente a sus recursos.
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 g) Informacin de productos potenciales
    .row.justify-content-center.mb-0
      .col-lg-7.order-lg-1.order-2
        p(data-aos="fade-down") Para medir el impacto de un producto en el desarrollo local, se requiere seleccionar al menos dos productos potenciales, es decir, tesoros con valor agregado o posibilidad de generarlo, que puedan ser producidos o comercializados por la comunidad.        
        p(data-aos="fade-down") Analizar productos que an no estn en el mercado permite aprovechar la creatividad surgida en las fases anteriores, identificando oportunidades para convertirlos en productos estrella que impulsen la economa local. La informacin bsica de cada producto potencial debe incluir:
        .tarjeta.tarjeta--C01.p-4.mb-3
          ul.lista-ul.mb-0(data-aos="fade-down")
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Proceso de produccin paso a paso y costos.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Ventas actuales (cantidad, precio y clientes).
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Emprendimientos o personas responsables del producto.
      .col-lg-5.order-lg-2.order-1.mb-3.col-md-5.col-7
        figure(data-aos="flip-down") 
          img(src='@/assets/curso/temas/tema4/img38.png', alt='Imagen decorativa')
