          h3.mb-0.ms-3 h) Elaboracin de una Cadena de Valor Simple
    p(data-aos="fade-down") La cadena de valor describe las actividades que permiten conducir un producto o servicio desde su concepcin hasta la entrega al consumidor final, incluyendo su disposicin o reciclaje. En el enfoque OVOP se propone trabajar con una Cadena de Valor Simple, compuesta por cuatro fases o eslabones que facilitan la comprensin y planificacin de los procesos productivos.
    p.mb-5(data-aos="fade-down") Estas etapas reflejan la integracin de la comunidad en cada fase, desde la produccin hasta la comercializacin, promoviendo la generacin de valor compartido, como se presenta en la siguiente figura:
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 6.
          span <em>Cuatro eslabones de una Cadena de Valor Simple</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img39.svg', alt='En la figura 6 se presentan los cuatro eslabones que conforman una Cadena de Valor Simple. Los eslabones, presentados en secuencia, son: diseo de producto, produccin, marketing y, finalmente, consumo y reciclaje. Representan el flujo bsico que sigue un bien desde su creacin hasta su uso y recuperacin.')
            figcaption Nota. Adaptado de la Gua 5 OVOP Colombia (SENA, JICA, MinCIT, 2020 ).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img40.svg', alt='En la figura 6 se presentan los cuatro eslabones que conforman una Cadena de Valor Simple. Los eslabones, presentados en secuencia, son: diseo de producto, produccin, marketing y, finalmente, consumo y reciclaje. Representan el flujo bsico que sigue un bien desde su creacin hasta su uso y recuperacin.')
          figcaption Nota. Adaptado de la Gua 4 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    p(data-aos="fade-down") Con los conocimientos descritos se busca comprender los conceptos fundamentales de la Gua para el estudio de Cadena de Valor Simple, fortaleciendo las capacidades comunitarias para la planificacin y el desarrollo local.
    p.mb-5(data-aos="fade-down") En la gua de Cadena de Valor Simple se proponen dos metodologas para el anlisis de la misma, sin embargo, se deja a discrecin del instructor integrar otras metodologas que considere pertinentes para tal fin, que hagan parte del ecosistema formativo del SENA.
