    separador
    #t_4_5.titulo-segundo.color-acento-contenido.mb-0(data-aos="flip-down")
      h2 4.5 Gua fortalecimiento de lderes OVOP
    .row.BG05.align-items-center.pt-5.pb-5.mb-4
      .col-lg-12.col-11.px-lg-5.px-4 
        p(data-aos="fade-down") Esta gua consta de cinco mdulos y tiene como objetivo orientar a los instructores de OVOP Colombia en la transferencia de conocimientos hacia la comunidad, para fortalecer las competencias de los lderes comunitarios. De esta manera, los participantes pueden convertirse en lderes OVOP, capaces de promover el desarrollo local mediante la autogestin, la participacin y la creatividad.
        .row.justify-content-center.mb-0(data-aos="flip-down")
          .col-lg-10.col-12
            .titulo-sexto.color-acento-contenido
              h5 Tabla 6.
              span <em>Mdulos de la Gua fortalecimiento de lderes OVOP</em>
            .tabla-a.color-acento-contenido
              table
                thead
                  tr
                    th(data-aos="fade-down")(style= "width: 50%") Mdulo
                    th(data-aos="fade-down")(style= "width: 50%") Contenido
                tbody
                  tr
                    td.text-bold(data-aos="fade-down")(style="background-color: #F6F6F6") 1
                    td(data-aos="fade-down")(style="background-color: #F6F6F6") Generalidades de OVOP
                  tr
                    td.text-bold(data-aos="fade-down")(style="background-color: #FFFFFF") 2
                    td(data-aos="fade-down")(style="background-color: #FFFFFF") Liderazgo colectivo
                  tr
                    td.text-bold(data-aos="fade-down")(style="background-color: #F6F6F6") 3
                    td(data-aos="fade-down")(style="background-color: #F6F6F6") Herramientas para fortalecer orgullo y confianza
                  tr
                    td.text-bold(data-aos="fade-down")(style="background-color: #FFFFFF") 4
                    td(data-aos="fade-down")(style="background-color: #FFFFFF") Planificacin participativa
                  tr
                    td.text-bold(data-aos="fade-down")(style="background-color: #F6F6F6") 5
                    td(data-aos="fade-down")(style="background-color: #F6F6F6") Construccin de buenas prcticas
              figcaption Nota. Tomado de la Gua 5 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 a) Liderazgo colectivo.
    p(data-aos="fade-down") En el enfoque OVOP, los miembros de cada territorio se convierten en actores clave del cambio positivo, promoviendo una visin de desarrollo basada en la autogestin, la creatividad y la cooperacin. Un liderazgo efectivo en este contexto requiere que los lderes comprendan profundamente la filosofa OVOP, para que su contribucin impacte de manera sostenible a la comunidad.
    p.mb-4(data-aos="fade-down") El liderazgo colectivo implica que varios lderes asuman roles complementarios dentro de la comunidad, fomentando la participacin y fortaleciendo las capacidades locales.
    .col-lg-4.col-md-5.col-auto
      .titulo-sexto.color-acento-contenido01.etiqueta01
        h5 Elementos fundamentales para ser un lder OVOP
    ul.lista-ul.mb-4(data-aos="fade-down")
      li.mb-0
        i.lista-ul__vineta
        p.mb-0 Entiende y cree en OVOP: aunque una persona sea carismtica, si no cree en OVOP, se le dificulta ser lder de OVOP.
      li.mb-0
        i.lista-ul__vineta
        p.mb-0 Cree y ampla la visin de sus comunidades: permite que sus comunidades imaginen sus posibilidades personales y colectivas en el territorio.
      li.mb-0
        i.lista-ul__vineta
        p.mb-0 Conoce de su propia comunidad: OVOP no es nicamente para mejorar los productos o servicios de la comunidad. OVOP es desarrollo incluyente. El conocimiento de las personas de su propio territorio es fundamental para lograr el desarrollo incluyente. 
      li.mb-0 
        i.lista-ul__vineta
        p.mb-0 Acta por s mismo: busca apoyo para fortalecer el conocimiento en la comunidad y aumenta las actividades tales como ferias, eventos, y charlas para estudiantes. 
    p.mb-5(data-aos="fade-down") Tipos de Lideres OVOP Colombia y sus caractersticas: en la experiencia del proyecto de OVOP Colombia, se ha presentado dificultades para generar buenos resultados con solo un lder. En cambio, contando con varios lderes de diferentes tipos, colaborando con los miembros de la comunidad y las entidades pblicas generando sinergias, hay mayor probabilidad de lograr el objetivo de manera efectiva. Los tipos de lderes que pueden contribuir al desarrollo local son los siguientes:
    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12.mb-5.mb-md-0.order-lg-2.order-2.mb-lg-0
        LineaTiempoD.color-acento-botones.especial
          .row.mb-0(numero="1" titulo="Lder Comunitario")
            .col-12.mb-0
              .tarjeta.tarjeta--blanca.p-4
                ul.lista-ul.mb-0(data-aos="fade-down")(style="color:#1E2AD5")
                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Entiende a su comunidad - dinmica social, historia y cultura, tesoros y recursos naturales, sueos y necesidades. 
                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Organiza y concientiza a las personas de acuerdo a sus potencialidades y habilidades, generando apoyos mutuos de acuerdo con los principios de OVOP. 
                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Es capaz de retroalimentar y comunicarse de manera adecuada con su comunidad. 
                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Estimula de manera positiva las actuaciones de la comunidad. 
                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Realiza actividades de integracin con otras poblaciones.
            .col-12.mb-0(data-aos="flip-down")
              figure
                img(src='@/assets/curso/temas/tema4/img41.jpg', alt='Imagen decorativa')
          .row(numero="2" titulo="Lder empresarial")
            .col-12.mb-0
              .tarjeta.tarjeta--blanca.p-4
                ul.lista-ul.mb-0(data-aos="fade-down")(style="color:#1E2AD5")
                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Genera procesos de innovacin y diversificacin alrededor de productos o servicios. 
