                  li.mb-0
                    i.lista-ul__vineta
                    p.mb-0 Tiene conocimientos y habilidades sobre un tema especfico o sector(es), por ejemplo: lcteo o turismo.
            .col-12.mb-0(data-aos="flip-down")
              figure
