    p.mb-5(data-aos="fade-down") A continuacin, se presentan las acciones esenciales para el fortalecimiento del liderazgo colectivo:
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 7.
          span <em>Acciones para el liderazgo colectivo</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img44.svg', alt='La figura 7 presenta tres acciones clave del liderazgo OVOP: actualizarse, comunicarse y delegar. Estas promueven la planificacin y revisin de actividades, la comunicacin con otros lderes y la delegacin de tareas para fortalecer el trabajo colectivo.')
            figcaption Nota. Adaptado de la Gua 5 OVOP Colombia (SENA, JICA, MinCIT, 2020).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img45.svg', alt='La figura 7 presenta tres acciones clave del liderazgo OVOP: actualizarse, comunicarse y delegar. Estas promueven la planificacin y revisin de actividades, la comunicacin con otros lderes y la delegacin de tareas para fortalecer el trabajo colectivo.')
          figcaption Nota. Adaptado de la Gua 5 OVOP Colombia (SENA, JICA, MinCIT, 2020 ).
    p.mb-5(data-aos="fade-down") Para el taller de clasificacin de los tipos de liderazgo remtase a la gua OVOP correspondiente.
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 b) Fortalecimiento del orgullo y la confianza.
    p.mb-5(data-aos="fade-down") En OVOP Colombia, fortalecer el orgullo y la confianza sobre el territorio es esencial para consolidar la identidad y cohesin comunitaria. Este proceso implica reconocer los valores, la historia y los recursos que hacen nica a cada comunidad.
    .row.justify-content-center
      .col-lg-10.col-12
        .cajon.cajon.C01.p-4.mb-3
          .row.justify-content-center
            .col-lg-10.col-12
              p.mb-3(data-aos="fade-down") Fortalecer la confianza, implica:
              ul.lista-ul.mb-0.mt-3(data-aos="fade-down")
                li.mb-0
                  i.lista-ul__vineta
                  p.mb-0 <b>Redes de solidaridad</b>: espacio de intercambio de ideas, informacin entre los actores comunitarios, permitiendo la transformacin de sus recursos en productos. 
                li.mb-0
                  i.lista-ul__vineta
                  p.mb-0 <b>Valor agregado</b>: desarrollo de una estrategia colectiva sobre un producto o servicio.
    p.mb-5(data-aos="fade-down") Conceptos claves para fortalecer el orgullo y la confianza:
    .tarjeta.tarjeta--BG06.p-md-5.p-4.mb-5(data-aos="flip-down")
      .tarjeta.tarjeta--blanca.p-4.p-md-5
        PasosB.color-acento-botones 
          .row.justify-content-center.p-4(titulo="")
            .col-lg-6.order-lg-1.order-2
              h4 Tesoro
              p En OVOP, se considera tesoro aquello que es la herencia de una comunidad, que ya desapareci o que se mantiene actualizado o transmitido a las generaciones presentes y futuras. Son recursos tangibles o intangibles. Se incluyen recursos culturales, tales como: danzas, artes, leyendas, patrimonio arqueolgico, y paisaje natural o ambiental. El tesoro del territorio debe ser el orgullo de la comunidad, no de un individuo.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema4/img46.jpg', alt='Imagen decorativa')
          .row.justify-content-center.p-4(titulo="")
            .col-lg-6.order-lg-1.order-2
              h4 Valor agregado
              p Es una caracterstica o servicio extra que se le da a un recurso, con el fin de aumentar su valor comercial. Esta caracterstica o servicio adicional o agregado hace de este recurso un producto nico o estrella. OVOP pone importancia en fortalecer la alianza dentro y fuera de la comunidad.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema4/img47.jpg', alt='Imagen decorativa')
          .row.justify-content-center.p-4(titulo="")
            .col-lg-6.order-lg-1.order-2
              h4 Productos nicos o estrellas
              p Significa normalmente un producto originario de un lugar determinado que tiene ventaja competitiva, en una regin o un pas. Sin embargo, aunque los productos tengan una caracterstica especial, no se pueden convertir automticamente en un producto de OVOP. En OVOP, se desarrollarn estos productos en la comunidad, con una visin positiva/ proactiva, mediante el trabajo conjunto de las entidades gubernamentales y privadas, para convertir su propio producto en el producto nico o estrella.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema4/img48.jpg', alt='Imagen decorativa')
    .row.justify-content-center.mb-5
      .col-lg-10.col-12
        .cajon.cajon.C02.p-4.mb-0
          .row.justify-content-center
            .col-lg-11.col-12
              p.mb-3(data-aos="fade-down") Con el fin realizar el taller para construir el orgullo y la confianza, remitirse al taller prctico <b>"Construimos nuestro orgullo y confianza"</b> que se encuentra en la gua de estudio.
    .row.align-items-center.mb-5(data-aos="flip-down")
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/ico.svg', style="width: 55px" ,alt='').m-auto
      .col-lg-auto.col-9.titulo01(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 c) Construir buenas prcticas para el desarrollo comunitario incluyente.
    p.mb-5(data-aos="fade-down") Para consolidar la sostenibilidad del enfoque OVOP, es necesario promover buenas prcticas de gestin comunitaria. Una herramienta clave es el ciclo PHVA (Planificar, Hacer, Verificar y Actuar), que permite administrar de forma participativa los planes de accin locales.
    .row.justify-content-center.mb-5(data-aos="flip-down")
      .col-lg-10.col-12
        .titulo-sexto.color-acento-contenido
          h5 Figura 8.
          span <em>Ciclo de PHVA - Planificar, Hacer, Verificar, y Actuar</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema4/img49.svg', alt='La figura 8 representa el ciclo PHVA, conformado por cuatro etapas: planificar, hacer, verificar y actuar, utilizadas para gestionar y mejorar continuamente las acciones comunitarias en OVOP.')
            figcaption Nota.Tomado de la Gua 5 OVOP Colombia (SENA, JICA, MinCIT, 2020).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema4/img50.svg', alt='La figura 8 representa el ciclo PHVA, conformado por cuatro etapas: planificar, hacer, verificar y actuar, utilizadas para gestionar y mejorar continuamente las acciones comunitarias en OVOP.')
          figcaption Nota.Tomado de la Gua 5 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    .row.justify-content-center.mb-0(data-aos="flip-down")
      .col-xl-3.col-lg-8.mb-4.mb-xl-5
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/temas/tema4/img51.svg", alt="alt").img130.m-auto
          h4.text-center Planificar
          p.mb-0 Definir objetivos, actividades, responsables, aliados y plazos.
      .col-xl-3.col-lg-8.mb-4.mb-xl-5
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/temas/tema4/img52.svg", alt="alt").img130.m-auto
          h4.text-center Hacer
          p.mb-0 Ejecutar las actividades del plan de accin comunitario.
      .col-xl-3.col-lg-8.mb-4.mb-xl-5
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/temas/tema4/img53.svg", alt="alt").img130.m-auto
          h4.text-center Verificar
          p.mb-0 Hacer seguimiento y evaluar avances.
      .col-xl-3.col-lg-8.mb-4.mb-xl-5
        .crd.crd--grayGrad
          .bottomCircle.mb-5
            figure
              img(src="@/assets/curso/temas/tema4/img54.svg", alt="alt").img130.m-auto
          h4.text-center Actuar
          p.mb-0 Realizar los ajustes necesarios en tiempo, recursos o contenidos.
    p.mb-5(data-aos="fade-down") Para facilitar la aplicacin prctica de los contenidos y promover el desarrollo de habilidades de liderazgo en los territorios, se presenta la siguiente ficha tcnica. Este recurso sirve como gua operativa para planificar, ejecutar y evaluar los talleres de fortalecimiento de lderes OVOP, garantizando una formacin integral basada en la participacin activa, la reflexin y la construccin colectiva del conocimiento.
    .row.justify-content-center.mb-5
      .col-12
        .titulo-sexto.color-acento-contenido
          h5 Tabla 7.
          span <em>Ficha tcnica para el desarrollo del fortalecimiento de lderes OVOP</em>
        .tabla-a.color-acento-contenido
          table
            thead
              tr
                th(colspan="3")(data-aos="fade-down") ENFOQUE DE DESARROLLO LOCAL OVOP COLOMBIA
            tbody
              tr
                th(colspan="3").text-bold(data-aos="fade-down") GUIA DE FORTALECIMIENTO DE LIDERES OVOP
              tr
                td(colspan="3").text-bold(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Objetivo del taller</b>: profundizar en el entendimiento del concepto bsico de OVOP.
              tr
                td(colspan="3").text-bold(data-aos="fade-down") <b>Condiciones previas:</b>
                  ul.lista-ul.mb-0(data-aos="fade-down")
                    li.mb-0
                      i.lista-ul__vineta
                      p.mb-0 Participantes: el facilitador de OVOP Colombia invita al grupo base con los lderes comunitarios.
                    li.mb-0
                      i.lista-ul__vineta
                      p.mb-0 Logstica: el saln debe tener capacidad para trabajar con ms de 20 personas, mobiliario (sillas, pupitres y mesas), pizarra, marcadores/colores, lpices, cinta de enmascarar, papelgrafos para escribir y colocar en la pared, proyector o vdeo beam, etc.
                    li.mb-0
                      i.lista-ul__vineta
                      p.mb-0 La orientacin de este taller est organizada por cinco mdulos para facilitar la orientacin a los facilitadores, se remienda iniciar por el mdulo 1. Sin embargo, se deja a discrecin del facilitador aplicar los mdulos de acuerdo a las necesidades de la comunidad.
            tbody(style="background-color:#D9E6FF")
              tr
                th(colspan="3")(data-aos="fade-down") Agenda y resultados esperados
            tbody
              tr
                th(data-aos="fade-down")(style="background-color: #F6F6F6") Actividad
                th(data-aos="fade-down")(style="background-color: #F6F6F6") Tiempo
                th(data-aos="fade-down")(style="background-color: #F6F6F6") Resultados
              tr
                td.text-bold(data-aos="fade-down") Registro de participantes.
                td(data-aos="fade-down") 15 minutos
                td(data-aos="fade-down") Registro de asistencia.
              tr
                td.text-bold(data-aos="fade-down")(style="background-color: #F6F6F6") Introduccin.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 30 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Descripcin general de la gua.
              tr
                td(colspan="3").text-bold(data-aos="fade-down") Mdulo I
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte I</b>: conceptos claves, el facilitador de OVOP Colombia presentar el concepto bsico de OVO.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 20 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte II</b>: los participantes se dividirn en dos (2) grupos (del mismo municipio o mixto). Cada participante, iniciar dibujando un bien o producto (arquitectura, fiesta, naturaleza, etc.) que se encuentra en su comunidad y que lo haga sentirse orgulloso o especial.
                  p Los integrantes expondrn su dibujo a los otros participantes en el mismo grupo, explicando por qu sienten orgullo o consideran especial ese bien o producto. 
                  p Posteriormente, cada grupo escoger el bien o producto que les haya parecido ms impactante y socializar.
                td(data-aos="fade-down") 60 minutos
                td(data-aos="fade-down") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte III</b>: estudio de caso, se mostrar el video producido por DNP Estrategia alternativa para el desarrollo local incluyente y el video IRODORI.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 20 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte IV</b>: retroalimentacin, en este espacio los participantes realizarn primero una reflexin, despus trabajarn el formato de evaluacin que se encuentra en la gua.
                td(data-aos="fade-down") 20 minutos
                td(data-aos="fade-down") Sensibilizacin de conceptos.
              tr
                td(colspan="3").text-bold(data-aos="fade-down") Mdulo II
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte I</b>: conceptos claves, socializar el concepto de liderazgos colectivos que es clave para lograr el mayor avance en el desarrollo comunitario.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 20 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin del concepto de liderazgo colectivo.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte II</b>: taller prctico, presenta la comprensin del papel de lder OVOP y el funcionamiento de los liderazgos colectivos.
                  p Discutir quien asumir las responsabilidades en cada estrategia aplicar.
                  p Socializar los resultados.
                td(data-aos="fade-down") 60 minutos
                td(data-aos="fade-down") Cada participante se identifica con el tipo de lder con el que se sienta cmodo, y escribe su nombre en el formato siguiente. Despus, cada grupo de liderazgo identifique tres estrategias para solucionar el problema.
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte III</b>: estudio de casos, presentar un estudio de caso donde se evidencien los diferentes tipos de liderazgo.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 20 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte IV</b>: retroalimentacin.
                td(data-aos="fade-down") 15 minutos
                td(data-aos="fade-down") Conclusiones y cierre.
              tr
                td(colspan="3").text-bold(data-aos="fade-down") Mdulo III Elaboracin del proyecto para sueo colectivo
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte I</b>: conceptos clave.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 30 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte II</b>: taller prctico Elaboremos un proyecto juntos para el futuro de nuestro territorio.
                  p Los participantes del taller conforman varios grupos de trabajo. Luego, facilitadores explican los puntos expuestos en la gua para trabajar en la elaboracin del proyecto.
                td(data-aos="fade-down") 20 minutos
                td(data-aos="fade-down") Imaginar del sueo colectivo.
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte III</b>: estudio de casos, los facilitadores comparten algunos proyectos que han contribuido a la construccin del sueo colectivo de otros municipios, para ampliar el conocimiento.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 40 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte IV</b>: retroalimentacin.
                td(data-aos="fade-down") 15 minutos
                td(data-aos="fade-down") Conclusiones y cierre.
              tr
                td(colspan="3").text-bold(data-aos="fade-down") Mdulo IV: fortalecimiento nuestro orgullo y confianza
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte I</b>: conceptos clave. En el mdulo 4, el (experto o instructor o responsable) desarrollar la tcnica para fortalecer el orgullo y la confianza de la comunidad.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 30 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte II</b>: taller prctico "Construimos nuestro orgullo y confianza".
                  p En este taller, se va a identificar los productos basados en el <em>slogan</em> de OVOP Colombia: "Mi Pueblo, Mi Producto, Mi Orgullo". 
                  p Los participantes conforman grupos de trabajo.
                  p Cada grupo muestra las caractersticas del municipio (la iniciativa) basado en el <em>slogan</em> de OVOP - "Mi Pueblo, Mi Producto, Mi Orgullo". Se trabajar en un pliego de papel <em>bond</em>.
                td(data-aos="fade-down") 25 minutos
                td(data-aos="fade-down") Listado de nuevos productos descubiertos en la comunidad.
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 
                  p <b>Parte III</b>: Las herramientas para fortalecer nuestro orgullo y confianza.
                  p La gua, sugiere tres herramientas. El facilitador puede adaptar otras segn las condiciones y necesidades de la comunidad.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 20 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin en herramientas para fortalecer el orgullo y confianza comunitarios.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte IV</b>: retroalimentacin.
                td(data-aos="fade-down") 15 minutos
                td(data-aos="fade-down") Conclusiones y cierre.
              tr
                td(colspan="3").text-bold(data-aos="fade-down") Mdulo V: construyamos buenas prcticas para el desarrollo comunitario incluyente
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") <b>Parte I</b>: conceptos clave, socializar los conceptos de PHVA.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 30 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Sensibilizacin de conceptos.
              tr
                td(data-aos="fade-down") 
                  p <b>Parte II</b>: taller prctico, se conformar un grupo de trabajo.
                  p Cada grupo presentar la idea del proyecto. Se trabajar en un pliego de papel <em>bond</em>: las actividades especficas, responsable, aliados estratgicos, recursos (tcnicos/financieros).
                td(data-aos="fade-down") 40 minutos
                td(data-aos="fade-down") Se seleccionar uno para un grupo vulnerable, por ejemplo: mujeres, jvenes, campesinos, etc. Se socializar o compartir el resultado a otros grupos.
              tr
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 
                  p <b>Parte III</b>: estudio de casos, los facilitadores comparten algunos proyectos que han contribuido a la construccin del sueo colectivo de otros municipios.
                td(data-aos="fade-down")(style="background-color: #F6F6F6") 20 minutos
                td(data-aos="fade-down")(style="background-color: #F6F6F6") Socializar casos de xito.
